export default function projects(){

    const TitleText = styled.p`
    font-weight: 600;
    font-size: 35px;
    color: #448bb4;
    `;
   
    const settingsForProjects = {
        dots: true,
        infinite: true,
        arrows: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        autoplay: true,
        autoplaySpeed: 2000,
        responsive: [
          {
            breakpoint: 900,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
            },
          },
        ],
      };
      
            return(

                <Container maxWidth="xl">
                <div id="projects" sty>
                  <TitleText>Our Projects</TitleText>
                  <Slider {...settingsForProjects}>
                    {projesct &&
                      projesct.map((eachProject) => <ProjectCard {...eachProject} />)}
                  </Slider>
                </div>
              </Container>
            
            );
}